import React, { useState, useEffect } from 'react'
import ReactTable from "react-table"
import { CardBody, Button, Col } from "reactstrap";
import Card from '@material-ui/core/Card';
import DataTablePagination from "../../../../components/DatatablePagination"
import mTrans from '../../../../Controler/c_Transaksi'
import Notifikasi from '../../../../helpers/Notifikasi'
import { isArray } from "util";
import numberWithCommas from "reactnumberwithcommas";
import moment from 'moment-timezone'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';




const Componen = (props) => {
    const authLogin = props.authLogin
    const [isReload, setIsReload] = useState(true)
    const [listLayanan, setlistLayanan] = useState([])
    const [isModal, setIsModal] = useState(false)
    const [dataSelect, setDataSelect] = useState({})
    const [jumlahBayar, setJumlahBayar] = useState("")
    const [isConfrim, setIsConfrim] = useState(false)

    useEffect(() => {
        setlistLayanan([])
        mTrans.listPembayaran(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, moment(props.tanggal).format("YYYY-MM-DD")).then(resp => {
            if (resp) setlistLayanan(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsReload(false));
        return () => {

        }
    }, [isReload, authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, props.tanggal])

    const dataTableColumns = [
        {
            Header: "Pasien",
            accessor: "pasien",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "Jumlah Obat",
            accessor: "totObat",
            Cell: props => <p className="text-left link" onClick={handleDet.bind(this, props.original)}> {props.value} Obat</p>
        },
        {
            Header: "Total Bayar",
            accessor: "totBayar",
            Cell: props => <p className="text-left"> {numberWithCommas(props.value)}</p>
        },
        {
            Header: <p className="text-left">Aksi</p>,
            Cell: props => <p className="text-muted">
                <Button style={{ marginRight: 3 }} color="warning" onClick={handleDet.bind(this, props.original)}>Pembayaran / Detail</Button>
            </p>
        }
    ];

    const handleDet = data => {
        setDataSelect(data)
        setIsModal(true)
    }
    const closeFrom = () => {
        setIsModal(false)
    }
    const closConfirm = () => {
        setIsConfrim(false)
    }
    const cofirmasiBayar = () => {
        if (dataSelect.totBayar >= jumlahBayar) return Notifikasi("warning", "Jumlah pembayaran harus sama atau lebih besar dari tagihan")
        setIsConfrim(true)
    }
    const handleBayar = () => {
        mTrans.sudahBayar(authLogin.clientID, authLogin.token, dataSelect._id, dataSelect.totBayar, jumlahBayar).then(resp => {
            if (resp) {
                setIsReload(true)
                Notifikasi("success", `Berhasil melakukan pembayaran`, "")
                closConfirm()
                closeFrom(false)
            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Simpan")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Simpan")
            } else {
                console.log(err);
            }
        })
    }
    return (
        <React.Fragment>
            <Col xs={12}>
                <Card>
                    <CardBody>
                        <ReactTable
                            data={listLayanan}
                            columns={dataTableColumns}
                            defaultPageSize={5}
                            showPageJump={true}
                            PaginationComponent={DataTablePagination}
                            showPageSizeOptions={true}
                        />
                    </CardBody>
                </Card>
            </Col>
            {isModal && (
                <Dialog open={isModal} onClose={closeFrom} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">Detail</DialogTitle>
                    <DialogContent>
                        <h6>Tanggal : <b>{moment(dataSelect.janji.tanggalJanji).format("DD-MM-YYYY")}</b></h6>
                        <h6>Dokter : <b>{dataSelect.kodedokter}</b></h6>
                        <h6><b>Layanan</b> : <i>{numberWithCommas(dataSelect.layanan.map(el => el.qty * el.harga).reduce((a, b) => a + b, 0))}</i></h6>
                        {dataSelect.layanan.length > 0 && dataSelect.layanan.map((el, i) => (
                            <p index={i}>- {el.namaLayanan} * {el.qty}</p>
                        ))}
                        <h6><b>Obat Dari Dokter</b> : <i>{numberWithCommas(dataSelect.resepObatdokter.map(el => el.qty * el.harga).reduce((a, b) => a + b, 0))}</i></h6>
                        {dataSelect.resepObatdokter.length > 0 && dataSelect.resepObatdokter.map((el, i) => (
                            <p index={i}>- {el.namaProduk} * {el.qty}</p>
                        ))}
                        <h6><b>Peralatan</b> : <i>{numberWithCommas(dataSelect.peralatan.map(el => el.qty * el.harga).reduce((a, b) => a + b, 0))}</i></h6>
                        {dataSelect.peralatan.length > 0 && dataSelect.peralatan.map((el, i) => (
                            <p index={i}>- {el.namaProduk} * {el.qty}</p>
                        ))}
                        <h6><b>Total Tagihan</b> : <b><i>{numberWithCommas(dataSelect.totBayar)}</i></b></h6>
                        <TextField
                            required
                            autoFocus
                            margin="dense"
                            id="JumlahBayar"
                            label="Jumlah Bayar"
                            fullWidth
                            value={jumlahBayar}
                            variant="outlined"
                            type="number"
                            onChange={(e) => setJumlahBayar(e.target.value)}
                        />
                        <h6><b>Kembalian</b> : <i>{dataSelect.totBayar <= jumlahBayar && numberWithCommas(jumlahBayar - dataSelect.totBayar)}</i></h6>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={closeFrom} color="secondary">Close</Button>
                        <Button onClick={cofirmasiBayar} color="primary">Sudah Bayar</Button>
                    </DialogActions>
                    {/* konfirmasi */}
                    <Dialog open={isConfrim} onClose={() => closConfirm()} aria-labelledby="form-dialog-title">
                        <DialogTitle id="form-dialog-title">Perhatian</DialogTitle>
                        <DialogContent>
                            Yakin ini sudah melakukan pembayaran ?
                    </DialogContent>
                        <DialogActions>
                            <Button onClick={() => closConfirm()} color="secondary">Batal</Button>
                            <Button onClick={handleBayar} color="primary">Oke</Button>
                        </DialogActions>
                    </Dialog>
                </Dialog>
            )}
        </React.Fragment>
    )
}

export default Componen;

